import React, { PureComponent } from 'react'
import { Form, Button, Row, Col, Input, DatePicker } from 'antd'
import style from './style/Form.scss'
import { connect } from 'react-redux'
import { mapDispatchToProps } from '../redux/mapDispatchToProps'
import { CsxSelect, CsxSearch } from 'Form'
import {
  API_PRODUCTIONFREQUENCYS_BATCHTYPE_LIST,
  API_GET_MRP_CODEANDNAME,
  API_GET_PRODUCTION_CODEANDNAME,
  API_GET_LOCATION_CODEANDNAME,
  API_PRODUCTIONFREQUENCYS_PRODUCTIONLINE_LIST
} from 'api'
const { RangePicker } = DatePicker
import moment from 'moment'
@connect(
  state => ({ ...state.receiveAndReturnMaterial }),
  mapDispatchToProps
)
class App extends PureComponent {
  constructor() {
    super(...arguments)
  }
  // 改变当前的输入值
  onInputChange(values) {
    const { searchParams, update } = this.props
    update({ searchParams: { ...searchParams, ...values } })
  }
  // 查询数据
  query = () => {
    const { handleGetList, searchParams, size } = this.props
    let reqObj = { ...searchParams }
    for (let i in reqObj) {
      if (Object.prototype.toString.call(reqObj[i]) === '[object Object]') {
        reqObj[i] = reqObj[i].value
      }
    }
    delete reqObj.initTime
    handleGetList({ searchParams: { ...reqObj, page: 1, size } })
  }
  // 重置
  reset = () => {
    const { update } = this.props
    let nowDate = moment()
    let dateTimeStart = `${nowDate.format('YYYY-MM-DD ')} 00:00:00`,
      dateTimeEnd = `${nowDate.format('YYYY-MM-DD ')} 23:59:59`
    update({ searchParams: {
      initTime: [nowDate, nowDate],
      dateTimeStart,
      dateTimeEnd
    }, productCodeOptions: {} })
  }
  render() {
    const {
      searchParams = {},
      productCodeOptions,
      menuPermissionIds
    } = this.props
    const {
      locationSearchKey,
      orderCode,
      type,
      linePositionsCode,
      mrpPropKey,
      produceLineCode,
      batchNumber,
      initTime
    } = searchParams
    const typeOptions = [
      { key: '0', value: '领料' },
      { key: '1', value: '退料' },
      { key: '2', value: '系统退料' }
    ]
    return (
      <Form className={style.form}>
        <Row type='flex' justify='start'>
          <Col span={8}>
            <div className='commonInput '>
              <label htmlFor=''>领/退料单号：</label>
              <div className='box'>
                <Input
                  value={orderCode}
                  onChange={e => {
                    const {
                      target: { value = '' }
                    } = e
                    this.onInputChange({ orderCode: value })
                  }}
                />
              </div>
            </div>
          </Col>
          <Col span={8}>
            <CsxSelect
              data={typeOptions}
              label='类型'
              allowClear={true}
              selectItem={{ data: type, key: 'key', valueKey: 'value' }}
              onChangeSelect={value => {
                this.onInputChange({ type: value })
              }}
            />
          </Col>
          <Col span={8}>
            <div className='commonInput '>
              <label htmlFor=''>线边仓：</label>
              <div className='box'>
                <Input
                  value={linePositionsCode}
                  onChange={e => {
                    const {
                      target: { value = '' }
                    } = e
                    this.onInputChange({ linePositionsCode: value })
                  }}
                />
              </div>
            </div>
          </Col>
          <Col span={8}>
            <CsxSelect
              dateApi={API_GET_MRP_CODEANDNAME}
              label='评估类'
              allowClear={true}
              selectItem={{
                data: mrpPropKey,
                key: 'code',
                valueKey: ['code', 'name']
              }}
              onChangeSelect={value => {
                this.onInputChange({ mrpPropKey: value })
              }}
            />
          </Col>
          <Col span={8}>
            <CsxSearch
              dateApi={API_GET_PRODUCTION_CODEANDNAME}
              label='物料编码/名称'
              // showBoth={true}
              allowClear={true}
              searchParamsKey='product'
              selectItem={{
                data: productCodeOptions,
                key: 'code',
                valueKey: ['code', 'name']
              }}
              onChangeSelect={data => {
                const { value = '' } = data
                const { update } = this.props
                update({
                  searchParams: { ...searchParams, productSearchKey: value },
                  productCodeOptions: data
                })
              }}
              onSelect={data => {
                const { update } = this.props
                const { value = '' } = data
                update({
                  searchParams: { ...searchParams, productSearchKey: value },
                  productCodeOptions: data
                })
              }}
            />
          </Col>
          <Col span={8}>
            <CsxSelect
              dateApi={API_PRODUCTIONFREQUENCYS_PRODUCTIONLINE_LIST}
              label='产线编码'
              allowClear={true}
              selectItem={{
                data: produceLineCode,
                key: 'code',
                valueKey: ['code', 'name']
              }}
              onChangeSelect={value => {
                this.onInputChange({ produceLineCode: value })
              }}
            />
          </Col>
          <Col span={8}>
            <CsxSelect
              dateApi={API_PRODUCTIONFREQUENCYS_BATCHTYPE_LIST}
              label='生产波次'
              allowClear={true}
              selectItem={{
                data: batchNumber,
                key: 'code',
                valueKey: ['name']
              }}
              onChangeSelect={value => {
                this.onInputChange({ batchNumber: value })
              }}
            />
          </Col>
          <Col span={8}>
            <CsxSelect
              allowClear={true}
              dateApi={API_GET_LOCATION_CODEANDNAME}
              label='地点编码/名称'
              allowClear={true}
              selectItem={{
                data: locationSearchKey,
                key: 'code',
                valueKey: ['code', 'name']
              }}
              onChangeSelect={value => {
                console.log('value is', value)
                this.onInputChange({ locationSearchKey: value })
              }}
            />
          </Col>
          <Col span={16}>
            <div className={'commonInput showTimePicker showTimePickerNotSecond'}>
              <label>领/退料时间：</label>
              <div className='box' style={{ marginRight: 0 }}>
                <RangePicker
                  allowClear={true}
                  // showTime
                  value={initTime}
                  onChange={time => {
                    let dateTimeStart =
                      time &&
                      time[0] &&
                      `${moment(time[0]).format('YYYY-MM-DD 00:00:00')}`
                    let dateTimeEnd =
                      time &&
                      time[1] &&
                      `${moment(time[1]).format('YYYY-MM-DD 23:59:59')}`
                    this.onInputChange({
                      dateTimeStart,
                      dateTimeEnd,
                      initTime: time
                    })
                  }}
                  format='YYYY-MM-DD'
                />
              </div>
            </div>
          </Col>

          <Col span={8} push={0}>
            <Row type='flex' justify='end'>
              {menuPermissionIds &&
                menuPermissionIds.includes('receiveAndReturnMaterial_find') && (
                  <Button
                    type='primary'
                    className={style.mr10}
                    onClick={this.query}
                  >
                    查询
                  </Button>
                )}
                <Button type='primary' onClick={this.reset}>
                    重置
                  </Button>
            </Row>
          </Col>
        </Row>
      </Form>
    )
  }
}

export default Form.create()(App)



