import React, { PureComponent } from 'react'
import { Table } from 'antd'
import { connect } from 'react-redux'
import { mapDispatchToProps } from '../redux/mapDispatchToProps'
import { countTableScroll } from 'util'
import moment from 'moment'
@connect(
  state => ({
    ...state.receiveAndReturnMaterial,
    userLoation: state.userLoation
  }),
  mapDispatchToProps
)
export default class app extends PureComponent {
  constructor(props) {
    super(props)
    this.state = {}
    this.columns = [
      {
        title: '类型',
        width: 80,
        dataIndex: 'type',
        key: 'type'
      },

      {
        title: '领/退料单号',
        width: 150,
        dataIndex: 'orderCode',
        key: 'orderCode'
      },
      {
        title: '产线编码',
        width: 80,
        dataIndex: 'produceLineCode',
        key: 'produceLineCode'
      },
      {
        title: '地点编码',
        width: 80,
        dataIndex: 'locationCode',
        key: 'locationCode'
      },
      {
        title: '地点名称',
        width: 140,
        dataIndex: 'locationName',
        key: 'locationName'
      },
      {
        title: '生产波次',
        width: 100,
        dataIndex: 'batchName',
        key: 'batchName'
      },
      {
        title: '物料编码',
        width: 100,
        dataIndex: 'productCode',
        key: 'productCode',
        render: (text, record, index) => text
      },
      {
        title: '物料名称',
        width: 150,
        dataIndex: 'productName',
        key: 'productName',
        render: (text, record, index) => text
      },
      {
        title: '单位',
        width: 60,
        dataIndex: 'unit',
        key: 'unit'
      },
      {
        title: '评估类',
        width: 120,
        dataIndex: 'mrpProp',
        key: 'mrpProp'
      },
      {
        title: '线边仓编码',
        width: 100,
        dataIndex: 'linePositionsCode',
        key: 'linePositionsCode',
        render: (text, record, i) => text
      },
      {
        title: '领/退料数量',
        width: 100,
        dataIndex: 'qty',
        key: 'qty'
      },
      {
        title: '领/退料员',
        width: 100,
        dataIndex: 'userBy',
        key: 'userBy'
      },
      {
        title: '领/退料时间',
        width: 180,
        dataIndex: 'datetime',
        key: 'datetime'
      },
      {
        title: '处理人',
        width: 100,
        dataIndex: 'manager',
        key: 'manager'
      }
    ]
  }
  componentDidMount() {
    const { handleGetList, searchParams, page, size, update } = this.props
    let nowDate = moment()
    let dateTimeStart = `${nowDate.format('YYYY-MM-DD ')} 00:00:00`,
      dateTimeEnd = `${nowDate.format('YYYY-MM-DD ')} 23:59:59`
    update({
      searchParams: {
        initTime: [nowDate, nowDate],
        dateTimeStart,
        dateTimeEnd
      }
    })
    handleGetList({ searchParams: { page, size, ...searchParams, dateTimeStart, dateTimeEnd } })
  }
  componentWillReceiveProps(newProps, newStates) {
    const { handleGetList, userLoation } = this.props
    const newUserLoationValue = newProps && newProps.userLoation && newProps.userLoation.value || ''
    const userLoationValue = userLoation && userLoation.value || ''
    if (newUserLoationValue != userLoationValue) {
      handleGetList({ searchParams: { page: 1, size: newProps.size, ...newProps.searchParams } })
    }
  }
  // 排序 columns排序列
  // isAsc 是否升序 true, false
  onChange = (pagination, filters, sorter, extra) => {
    const { handleGetList, searchParams, page, size } = this.props
    if (sorter.order) {
      if (sorter.order === 'ascend') {
        handleGetList({
          searchParams: {
            page,
            size,
            ...searchParams,
            isAsc: true,
            columns: sorter.field
          }
        })
      } else {
        handleGetList({
          searchParams: {
            page,
            size,
            ...searchParams,
            isAsc: false,
            columns: sorter.field
          }
        })
      }
    } else {
      // 判断是点跳页面还是排序
      let reqObj = { ...searchParams }
      for (let i in reqObj) {
        if (Object.prototype.toString.call(reqObj[i]) === '[object Object]') {
          reqObj[i] = reqObj[i].value
        }
      }
      delete reqObj.initTime
      handleGetList({
        searchParams: {
          page: pagination.current,
          size: pagination.pageSize,
          ...reqObj
        }
      })
    }
  }
  render() {
    const {
      total,
      page,
      size,
      tableLoading,
      selectedRowKeys,
      searchResult = []
    } = this.props
    let pagination = {
      total,
      showTotal: (total, range) => `共 ${total} 条`, // 显示总数
      showQuickJumper: true, // 是否可以快速跳转至某页
      current: page,
      pageSize: size,
      showSizeChanger: true, // 是否可以改变 pageSize
      className: 'pagination',
      pageSizeOptions: ['10', '50', '100', '200', '500']
    }
    let rowSelection = {
      selectedRowKeys,
      onChange: (selectedRowKeys, selectedRows) => {
        const { update } = this.props
        update({ selectedRowKeys, selectedRows })
      }
    }

    return (
      <div className='table-group'>
        <Table
          rowKey={record => record.orderCode + record.productCode }
          loading={tableLoading}
          columns={this.columns}
          dataSource={searchResult}
          scroll={{ x: countTableScroll(this.columns) }}
          onChange={this.onChange}
          pagination={pagination}
          rowSelection={rowSelection}
        />
      </div>
    )
  }
}



