import * as actionType from './actionType'
import { getData } from 'util'
import {
  API_RECEIVEANDRETURNMATERIALS_PAGE,
  API_RECEIVEANDRETURNMATERIALS_DET_SOURCE,
  API_RECEIVEANDRETURNMATERIALS_EXPORT
} from 'api'
//同步
export const update = object => {
  console.log('object is ', object)
  return {
    type: actionType.RECEIVEANDRETURNMATERIAL_UPDATE,
    data: object
  }
}

//异步
// 获取列表
export const handleGetList = reqData => dispatch => {
  dispatch({
    type: actionType.RECEIVEANDRETURNMATERIAL_UPDATE,
    data: { tableLoading: true }
  })
  const { searchParams = {} } = reqData
  for (let i in searchParams) {
    if (Object.prototype.toString.call(searchParams[i]) === '[object Object]') {
      searchParams[i] = searchParams[i].value
    }
  }
  getData({ api: API_RECEIVEANDRETURNMATERIALS_PAGE, ...reqData })
    .then(data => {
      // 相应的逻辑判断
      let { page, size } = reqData.searchParams
      dispatch({
        type: actionType.RECEIVEANDRETURNMATERIAL_UPDATE,
        data: {
          page,
          size,
          tableLoading: false,
          total: data.result.total,
          searchResult: data.result.list || []
        }
      })
    })
    .catch(err => {
      dispatch({
        type: actionType.RECEIVEANDRETURNMATERIAL_FAIL
      })
    })
}

// 获取需求明细
export const getSourceDetail = (reqData, callBack) => dispatch => {
  getData({ api: API_RECEIVEANDRETURNMATERIALS_DET_SOURCE, ...reqData })
    .then(data => {
      callBack && callBack()
    })
    .catch(err => {
      dispatch({
        type: actionType.RECEIVEANDRETURNMATERIAL_FAIL
      })
    })
}
// 导出
export const handleExport = (reqData, callBack) => dispatch => {
  dispatch({
    type: actionType.RECEIVEANDRETURNMATERIAL_UPDATE,
    data: { modalTuttonLoading: true }
  })
  getData({ api: API_RECEIVEANDRETURNMATERIALS_EXPORT, ...reqData })
    .then(data => {
      callBack && callBack()
      dispatch({
        type: actionType.RECEIVEANDRETURNMATERIAL_UPDATE,
        data: { modalTuttonLoading: false }
      })
    })
    .catch(err => {
      dispatch({
        type: actionType.RECEIVEANDRETURNMATERIAL_FAIL
      })
    })
}



